import { Component } from "libraries/ui/component";

function isComponent(obj: any): obj is Component {
  return obj.isComponent !== undefined;
}

export function render<T = HTMLElement>(children: Component | (Component | HTMLElement)[]): T {
  if (Array.isArray(children)) {
    return children.map((child) => {
      if (isComponent(child)) {
        return child.render();
      } else {
        return child;
      }
    }) as T;
  }

  if (!isComponent(children)) {
    throw new Error("Render argument must be component or array");
  }

  return children.render() as T;
}
