import { App, IAppContext } from "app/components/app.component";
import { startPwaServiceWorker } from "app/services/pwa/start-pwa-service-worker";
import "app/styles/global.css";
import { render } from "libraries/ui/render";
import { isDevelopment } from "app/services/is-development";
import { ScreenTransition } from "libraries/screen-transition";
import { SCREEN_TRANSITION_DISABLED_AREA_KEY } from "app/constants";
import { PermanentScreenSlider } from "libraries/screen-slider";
import { setContext } from "libraries/ui/context";
import { router, url } from "app/router";
import { history } from "app/router/history";

export function main() {
  if (!isDevelopment()) {
    startPwaServiceWorker();
  }

  const root = document.getElementById("root");

  if (root === null) {
    throw new Error("No root found.");
  }

  setContext<IAppContext>(App, {
    router,
    history,
    url,
    appScreenTransition: new ScreenTransition({
      disabledAreaSelectors: [`[${SCREEN_TRANSITION_DISABLED_AREA_KEY}]`],
    }),
    contentScreenSlider: new PermanentScreenSlider({
      allowedArea: 50,
      threshold: 0.3,
      animationDuration: 300,
    }),
  });

  root.append(render(new App()));
}
