export interface ScreenSliderOptions {
  allowedArea?: number;
  threshold?: number;
  animationDuration?: number;
  swipeEnabled?: boolean; // Общий флаг для свайпов
  allowSwipeNext?: boolean; // Флаг для свайпов на следующий экран
  allowSwipePrev?: boolean; // Флаг для свайпов на предыдущий экран
}

export interface ScreenItem {
  element: HTMLElement;
}

export interface TemporaryScreenItem {
  element: HTMLElement;
  isTemporary: boolean;
}
