import * as styles from "app/components/app.module.css";
import { defineComponent } from "libraries/ui/define-component";
import { div } from "libraries/ui/html-elements";
import { PermanentScreenSlider } from "libraries/screen-slider";
import { ScreenTransition } from "libraries/screen-transition";
import { WebComponent } from "libraries/ui/web-component";
import UniversalRouter from "universal-router";
import { Action, BrowserHistory } from "history";
import { RouteName, url } from "app/router";

interface IAppProps {}

export interface IAppContext {
  router: UniversalRouter;
  history: BrowserHistory;
  url: typeof url;
  contentScreenSlider: PermanentScreenSlider;
  appScreenTransition: ScreenTransition;
}

class AppComponent extends WebComponent<IAppProps> {
  private appScreenContainer?: HTMLDivElement;

  private get context(): IAppContext {
    return this.getContext<IAppContext>();
  }

  private unlistenHistoryChanges?: () => void;

  public async mounted(): Promise<void> {
    if (location.pathname !== this.context.url(RouteName.Home)) {
      this.context.history.replace(this.context.url(RouteName.Home));
    }

    const component: HTMLElement = await this.context.router.resolve({ pathname: location.pathname });

    this.context.appScreenTransition.init({
      container: this.appScreenContainer!,
      initialScreen: component,
    });

    this.context.appScreenTransition.onSwipeBack(() => {
      this.context.history.back();
    });

    this.unlistenHistoryChanges = this.context.history.listen(async ({ location, action }) => {
      const component: HTMLElement = await this.context.router.resolve({
        pathname: location.pathname,
      });

      if (action === Action.Pop) {
        await this.context.appScreenTransition.prev();

        return;
      }

      await this.context.appScreenTransition.next(component);
    });
  }

  public unmounted(): void {
    this.context.appScreenTransition.destroy();
    this.unlistenHistoryChanges?.();
  }

  public template(): HTMLElement {
    return div({
      id: "app-screen-container",
      class: styles.appScreenContainer,
      setRef: (ref: HTMLDivElement) => {
        this.appScreenContainer = ref;
      },
    });
  }
}

export const App = defineComponent<AppComponent, IAppProps>("app-root", AppComponent);
