import { ScreenItem, ScreenSliderOptions } from "libraries/screen-slider/types";

export abstract class BaseScreenSlider<TScreenItem extends ScreenItem> {
  options: ScreenSliderOptions;
  container!: HTMLElement;
  currentIndex: number;
  isDragging: boolean;
  startX: number;
  currentX: number;
  translateX: number;
  contentWidth: number;
  animations: Animation[];
  swipeDirection: "next" | "prev" | null;
  screenCache: TScreenItem[]; // Кэш для экранов
  isAnimating: boolean;
  handleTouchStartBound!: (event: TouchEvent) => void;
  handleTouchMoveBound!: (event: TouchEvent) => void;
  handleTouchEndBound!: (event: TouchEvent) => void;

  // Логика для свайпов и анимаций
  constructor(options: ScreenSliderOptions = {}) {
    const defaultOptions: ScreenSliderOptions = {
      allowedArea: 50,
      threshold: 0.3,
      animationDuration: 300,
      swipeEnabled: true,
      allowSwipeNext: true,
      allowSwipePrev: true,
    };
    this.options = { ...defaultOptions, ...options };
    this.screenCache = [];
    this.currentIndex = 0;
    this.isDragging = false;
    this.startX = 0;
    this.currentX = 0;
    this.translateX = 0;
    this.contentWidth = 0;
    this.animations = [];
    this.swipeDirection = null;
    this.isAnimating = false;
  }

  protected setupContainer() {
    this.container.style.position = "relative";
    this.container.style.overflow = "hidden";
  }

  protected setupScreens() {
    // Очищаем контейнер
    this.container.innerHTML = "";
    // Добавляем только текущий экран
    const currentScreen = this.screenCache[this.currentIndex].element;
    this.container.appendChild(currentScreen);

    // Применяем дефолтные стили
    this.applyDefaultStyles(currentScreen, this.currentIndex);
  }

  protected bindEvents() {
    this.handleTouchStartBound = this.handleTouchStart.bind(this);
    this.handleTouchMoveBound = this.handleTouchMove.bind(this);
    this.handleTouchEndBound = this.handleTouchEnd.bind(this);

    this.container.addEventListener("touchstart", this.handleTouchStartBound, { passive: true });
    this.container.addEventListener("touchmove", this.handleTouchMoveBound, { passive: false });
    this.container.addEventListener("touchend", this.handleTouchEndBound, { passive: true });
  }

  // Общий метод для очистки экранов
  protected cleanupScreens() {
    const screensInContainer = Array.from(this.container.children) as HTMLElement[];
    const currentScreen = this.screenCache[this.currentIndex].element;
    screensInContainer.forEach((screen) => {
      if (screen !== currentScreen) {
        this.container.removeChild(screen);
      }
    });
  }

  public init(container: HTMLElement, screens: HTMLElement[]) {
    this.container = container;

    if (screens.length === 0) {
      throw new Error("No screens provided");
    }

    this.initCache(screens);

    this.contentWidth = this.container.offsetWidth;

    this.setupContainer();
    this.setupScreens();
    this.bindEvents();

    window.addEventListener("resize", () => {
      this.contentWidth = this.container.offsetWidth;
    });
  }

  protected initCache(screens: HTMLElement[]) {
    this.screenCache = screens.map((screen, index) => {
      return {
        element: screen,
      } as TScreenItem;
    });
  }

  protected handleTouchStart(event: TouchEvent) {
    if (!this.options.swipeEnabled || this.isAnimating) return;

    const touchX = event.touches[0].clientX;
    const allowedArea = this.options.allowedArea!;
    const contentWidth = this.contentWidth;

    const currentScreenItem = this.screenCache[this.currentIndex];

    if (this.currentIndex > 0 && touchX < allowedArea && this.options.allowSwipePrev) {
      this.swipeDirection = "prev";
      // Добавляем предыдущий экран в контейнер
      const prevScreenItem = this.screenCache[this.currentIndex - 1];
      const prevScreen = prevScreenItem.element;
      if (!this.container.contains(prevScreen)) {
        this.container.appendChild(prevScreen);
        this.applyDefaultStyles(prevScreen, this.currentIndex - 1);
        prevScreen.style.transform = "translateX(-100%)";
      }
    } else if (this.currentIndex < this.screenCache.length - 1 && touchX > contentWidth - allowedArea && this.options.allowSwipeNext) {
      this.swipeDirection = "next";
      // Добавляем следующий экран в контейнер
      const nextScreenItem = this.screenCache[this.currentIndex + 1];
      const nextScreen = nextScreenItem.element;
      if (!this.container.contains(nextScreen)) {
        this.container.appendChild(nextScreen);
        this.applyDefaultStyles(nextScreen, this.currentIndex + 1);
        nextScreen.style.transform = "translateX(100%)";
      }
    } else {
      this.swipeDirection = null;
      return;
    }

    this.isDragging = true;
    this.startX = touchX;
    this.translateX = 0;

    this.cancelAnimations();
  }

  protected handleTouchMove(event: TouchEvent) {
    if (!this.options.swipeEnabled || !this.isDragging || !this.swipeDirection || this.isAnimating) return;

    this.currentX = event.touches[0].clientX;
    const deltaX = this.currentX - this.startX;
    event.preventDefault();

    const deltaPercent = (deltaX / this.contentWidth) * 100;

    const currentScreen = this.screenCache[this.currentIndex].element;

    if (this.swipeDirection === "next" && deltaX < 0 && this.options.allowSwipeNext) {
      const nextScreen = this.screenCache[this.currentIndex + 1].element;
      this.translateX = deltaPercent;
      currentScreen.style.transform = `translateX(${this.translateX}%)`;
      nextScreen.style.transform = `translateX(${100 + this.translateX}%)`;
    } else if (this.swipeDirection === "prev" && deltaX > 0 && this.options.allowSwipePrev) {
      const prevScreen = this.screenCache[this.currentIndex - 1].element;
      this.translateX = deltaPercent;
      currentScreen.style.transform = `translateX(${this.translateX}%)`;
      prevScreen.style.transform = `translateX(${-100 + this.translateX}%)`;
    }
  }

  protected handleTouchEnd(event: TouchEvent) {
    if (!this.options.swipeEnabled || !this.isDragging || !this.swipeDirection || this.isAnimating) return;
    this.isDragging = false;

    const threshold = this.options.threshold! * this.contentWidth;
    const absTranslateX = Math.abs((this.translateX / 100) * this.contentWidth);

    if (absTranslateX > threshold) {
      if (this.swipeDirection === "next" && this.options.allowSwipeNext) {
        this.animateToNext();
      } else if (this.swipeDirection === "prev" && this.options.allowSwipePrev) {
        this.animateToPrev();
      }
    } else {
      this.resetPosition();
    }
  }

  protected animateToNext() {
    if (this.isAnimating) return;
    this.isAnimating = true;

    const duration = this.options.animationDuration!;
    const currentScreen = this.screenCache[this.currentIndex].element;
    const nextScreen = this.screenCache[this.currentIndex + 1].element;

    this.cancelAnimations();

    // Убеждаемся, что следующий экран добавлен
    if (!this.container.contains(nextScreen)) {
      this.container.appendChild(nextScreen);
      this.applyDefaultStyles(nextScreen, this.currentIndex + 1);
      nextScreen.style.transform = "translateX(100%)";
    }

    const currentAnimation = currentScreen.animate([{ transform: `translateX(${this.translateX}%)` }, { transform: "translateX(-100%)" }], {
      duration: duration,
      easing: "ease-out",
      fill: "forwards",
    });

    const nextAnimation = nextScreen.animate([{ transform: `translateX(${100 + this.translateX}%)` }, { transform: "translateX(0%)" }], {
      duration: duration,
      easing: "ease-out",
      fill: "forwards",
    });

    this.animations.push(currentAnimation, nextAnimation);

    currentAnimation.onfinish = () => {
      currentScreen.style.transform = "translateX(-100%)";
      this.container.removeChild(currentScreen);
      this.currentIndex += 1;
      this.cleanupScreens();
      this.isAnimating = false;
    };

    nextAnimation.onfinish = () => {
      nextScreen.style.transform = "translateX(0%)";
      this.cleanupScreens();
    };
  }

  protected animateToPrev() {
    if (this.isAnimating) return;
    this.isAnimating = true;

    const duration = this.options.animationDuration!;
    const currentScreen = this.screenCache[this.currentIndex].element;
    const prevScreen = this.screenCache[this.currentIndex - 1].element;

    this.cancelAnimations();

    // Убеждаемся, что предыдущий экран добавлен
    if (!this.container.contains(prevScreen)) {
      this.container.appendChild(prevScreen);
      this.applyDefaultStyles(prevScreen, this.currentIndex - 1);
      prevScreen.style.transform = "translateX(-100%)";
    }

    const currentAnimation = currentScreen.animate([{ transform: `translateX(${this.translateX}%)` }, { transform: "translateX(100%)" }], {
      duration: duration,
      easing: "ease-out",
      fill: "forwards",
    });

    const prevAnimation = prevScreen.animate([{ transform: `translateX(${-100 + this.translateX}%)` }, { transform: "translateX(0%)" }], {
      duration: duration,
      easing: "ease-out",
      fill: "forwards",
    });

    this.animations.push(currentAnimation, prevAnimation);

    currentAnimation.onfinish = () => {
      currentScreen.style.transform = "translateX(100%)";
      this.container.removeChild(currentScreen);
      this.currentIndex -= 1;
      this.cleanupScreens();
      this.isAnimating = false;
    };

    prevAnimation.onfinish = () => {
      prevScreen.style.transform = "translateX(0%)";
      this.cleanupScreens();
    };
  }

  private resetPosition() {
    if (this.isAnimating) return;
    this.isAnimating = true;

    const duration = this.options.animationDuration! / 2;
    const currentScreen = this.screenCache[this.currentIndex].element;
    let otherScreenItem: ScreenItem | undefined;

    if (this.swipeDirection === "next") {
      otherScreenItem = this.screenCache[this.currentIndex + 1];
    } else if (this.swipeDirection === "prev") {
      otherScreenItem = this.screenCache[this.currentIndex - 1];
    }

    this.cancelAnimations();

    const currentAnimation = currentScreen.animate([{ transform: `translateX(${this.translateX}%)` }, { transform: "translateX(0%)" }], {
      duration: duration,
      easing: "ease-out",
      fill: "forwards",
    });

    if (otherScreenItem) {
      const otherScreen = otherScreenItem.element;
      const startTransform = this.swipeDirection === "next" ? `${100 + this.translateX}%` : `${-100 + this.translateX}%`;
      const endTransform = this.swipeDirection === "next" ? "100%" : "-100%";

      const otherAnimation = otherScreen.animate([{ transform: `translateX(${startTransform})` }, { transform: `translateX(${endTransform})` }], {
        duration: duration,
        easing: "ease-out",
        fill: "forwards",
      });

      this.animations.push(otherAnimation);

      otherAnimation.onfinish = () => {
        otherScreen.style.transform = `translateX(${endTransform})`;
        otherScreen.remove();
      };
    }

    this.animations.push(currentAnimation);

    currentAnimation.onfinish = () => {
      currentScreen.style.transform = "translateX(0%)";
      this.cleanupScreens();
      this.isAnimating = false;
    };
  }

  protected applyDefaultStyles(screen: HTMLElement, index: number) {
    screen.style.position = "absolute";
    screen.style.width = "100%";
    screen.style.height = "100%";
    screen.style.top = "0";
    screen.style.left = "0";
    screen.style.transform = `translateX(0%)`;
  }

  protected cancelAnimations() {
    this.animations.forEach((animation) => animation.cancel());
    this.animations = [];
  }

  // Дополнительные общие методы для свайпов
  public setSwipeEnabled(enabled: boolean) {
    this.options.swipeEnabled = enabled;
  }

  public setAllowSwipeNext(allowed: boolean) {
    this.options.allowSwipeNext = allowed;
  }

  public setAllowSwipePrev(allowed: boolean) {
    this.options.allowSwipePrev = allowed;
  }

  // Уничтожение слайдера
  public destroy() {
    this.cancelAnimations();
    this.container.removeEventListener("touchstart", this.handleTouchStartBound);
    this.container.removeEventListener("touchmove", this.handleTouchMoveBound);
    this.container.removeEventListener("touchend", this.handleTouchEndBound);
  }
}
