import { BaseScreenSlider } from "libraries/screen-slider/base-slider";
import { ScreenItem } from "libraries/screen-slider/types";

// Класс для постоянных экранов
export class PermanentScreenSlider extends BaseScreenSlider<ScreenItem> {
  public init(container: HTMLElement, screens: HTMLElement[], startIndex: number = 0) {
    // Проверка корректности startIndex
    if (startIndex < 0 || startIndex >= screens.length) {
      throw new Error(`startIndex ${startIndex} is out of bounds`);
    }

    this.currentIndex = startIndex;

    super.init(container, screens);
  }

  public addScreen(screen: HTMLElement) {
    if (!screen.id) {
      console.warn("The added screen does not have an identifier.");
      return;
    }

    const screenWithSameId = this.screenCache.some((existingScreen) => existingScreen.element.id === screen.id);

    if (screenWithSameId) {
      return;
    }

    this.screenCache.push({ element: screen });
  }

  public goToScreen(index: number) {
    if (this.isAnimating) return;

    if (index < 0 || index >= this.screenCache.length) {
      console.warn("Invalid screen index.");
      return;
    }

    if (index === this.currentIndex) {
      return;
    }

    const direction = index > this.currentIndex ? "next" : "prev";
    this.swipeDirection = direction as "next" | "prev";
    this.translateX = 0;

    const currentScreen = this.screenCache[this.currentIndex].element;
    const targetScreen = this.screenCache[index].element;

    if (!this.container.contains(targetScreen)) {
      this.container.appendChild(targetScreen);
      this.applyDefaultStyles(targetScreen, index);
      targetScreen.style.transform = direction === "next" ? "translateX(100%)" : "translateX(-100%)";
    }

    this.cancelAnimations();
    this.isAnimating = true;

    const duration = this.options.animationDuration!;

    const currentAnimation = currentScreen.animate(
      [{ transform: "translateX(0%)" }, { transform: direction === "next" ? "translateX(-100%)" : "translateX(100%)" }],
      {
        duration: duration,
        easing: "ease-out",
        fill: "forwards",
      }
    );

    const targetAnimation = targetScreen.animate(
      [{ transform: direction === "next" ? "translateX(100%)" : "translateX(-100%)" }, { transform: "translateX(0%)" }],
      {
        duration: duration,
        easing: "ease-out",
        fill: "forwards",
      }
    );

    this.animations.push(currentAnimation, targetAnimation);

    currentAnimation.onfinish = () => {
      currentScreen.style.transform = direction === "next" ? "translateX(-100%)" : "translateX(100%)";
      this.container.removeChild(currentScreen);
      this.currentIndex = index;

      this.cleanupScreens();
      this.isAnimating = false;
    };

    targetAnimation.onfinish = () => {
      targetScreen.style.transform = "translateX(0%)";
      this.cleanupScreens();
    };
  }
}
