import * as styles from "app/components/app.module.css";
import { IAppContext } from "app/components/app.component";
import { SCREEN_TRANSITION_DISABLED_AREA_KEY } from "app/constants";
import { button, div } from "libraries/ui/html-elements";
import { WebComponent } from "libraries/ui/web-component";
import { defineComponent } from "libraries/ui/define-component";
import { render } from "libraries/ui/render";

export interface INextScreenProps {}

class NextScreenComponent extends WebComponent<INextScreenProps> {
  private get context(): IAppContext {
    return this.getContext<IAppContext>();
  }

  public mounted(): void {
    this.classList.add(styles.nextScreen);
  }

  public template(): HTMLElement[] {
    return render([
      div("Следующий экран"), //
      button("Назад", {
        [SCREEN_TRANSITION_DISABLED_AREA_KEY]: "",
        onclick: () => {
          this.context.history.back();
          this.context.appScreenTransition.prev();
        },
      }),
      div({
        style: "height: 5rem; background-color: gray",
      }),
      div({
        style: "height: 5rem; background-color: white",
      }),
      div({
        style: "height: 5rem; background-color: gray",
      }),
      div({
        style: "height: 5rem; background-color: white",
      }),
      div({
        style: "height: 5rem; background-color: gray",
      }),
      div({
        style: "height: 5rem; background-color: white",
      }),
      div({
        style: "height: 5rem; background-color: gray",
      }),
      div({
        style: "height: 5rem; background-color: white",
      }),
      div({
        style: "height: 5rem; background-color: gray",
      }),
      div({
        style: "height: 5rem; background-color: white",
      }),
      div({
        style: "height: 5rem; background-color: gray",
      }),
      div({
        style: "height: 5rem; background-color: white",
      }),
      div({
        style: "height: 5rem; background-color: gray",
      }),
      div({
        style: "height: 5rem; background-color: white",
      }),
      div({
        style: "height: 5rem; background-color: gray",
      }),
    ]);
  }
}

export const NextScreen = defineComponent<NextScreenComponent, INextScreenProps>("next-screen", NextScreenComponent);
