import { HomeScreen } from "app/components/screens/home/home.component";
import { NextScreen } from "app/components/screens/next/next.component";
import { div } from "libraries/ui/html-elements";
import { render } from "libraries/ui/render";

export enum RouteName {
  Home = "home",
  Next = "next",
}

export const routes = [
  {
    path: "/",
    name: RouteName.Home,
    action: () => {
      return render(new HomeScreen());
    },
  },
  {
    path: "/next",
    name: RouteName.Next,
    action: () => {
      return render(new NextScreen());
    },
  },
  {
    path: "(.*)",
    action: () => div("Not found"),
  },
];
