import Sortable from "sortablejs";
import { defineComponent } from "libraries/ui/define-component";
import { div, span } from "libraries/ui/html-elements";
import * as styles from "app/components/screens/screen-2/screen-2.module.css";
import { PermanentScreenSlider } from "libraries/screen-slider";
import { WebComponent } from "libraries/ui/web-component";

interface IScreen2Props {
  class?: string;
  screenSlider: PermanentScreenSlider;
}

class Screen2Component extends WebComponent<IScreen2Props> {
  private tilesContainer?: HTMLDivElement;

  private sortableInstance?: Sortable;

  private setTilesContainer(ref: HTMLDivElement): void {
    this.tilesContainer = ref;
  }

  public unmounted(): void {
    if (this.sortableInstance !== undefined) {
      this.sortableInstance.destroy();
    }
  }

  public template(): HTMLElement {
    return div(
      div(
        div(span("1"), {
          class: styles.tile,
        }),
        div(span("2"), {
          class: styles.tile,
        }),
        div(span("3"), {
          class: styles.tile,
        }),
        div(span("4"), {
          class: styles.tile,
        }),
        div(span("5"), {
          class: styles.tile,
        }),
        div(span("6"), {
          class: styles.tile,
        }),
        div(span("7"), {
          class: styles.tile,
        }),
        div(span("8"), {
          class: styles.tile,
        }),
        div(span("9"), {
          class: styles.tile,
        }),
        div(span("10"), {
          class: styles.tile,
        }),
        div(span("11"), {
          class: styles.tile,
        }),
        div(span("12"), {
          class: styles.tile,
        }),
        div(span("13"), {
          class: styles.tile,
        }),
        div(span("14"), {
          class: styles.tile,
        }),
        div(span("15"), {
          class: styles.tile,
        }),
        div(span("16"), {
          class: styles.tile,
        }),
        div(span("17"), {
          class: styles.tile,
        }),
        div(span("18"), {
          class: styles.tile,
        }),
        {
          class: styles.gridContainer,
          setRef: (ref: HTMLDivElement) => {
            this.setTilesContainer(ref);
          },
        }
      ),
      {
        class: styles.screen,
      }
    );
  }

  public mounted(): void {
    this.id = "screen-2";

    if (this.props.class !== undefined) {
      this.classList.add(this.props.class);
    }

    if (this.tilesContainer === undefined) {
      return;
    }

    this.sortableInstance = new Sortable(this.tilesContainer, {
      ghostClass: styles.tileGhost,
      animation: 200,
      delayOnTouchOnly: true,
      delay: 150,
      scroll: true, // Enable the plugin. Can be HTMLElement.
      forceAutoScrollFallback: false, // force autoscroll plugin to enable even when native browser autoscroll is available
      scrollSensitivity: 30, // px, how near the mouse must be to an edge to start scrolling.
      scrollSpeed: 10, // px, speed of the scrolling
      bubbleScroll: true, // apply autoscroll to all parent elements, allowing for easier movement
      onChoose: () => {
        this.props?.screenSlider.setSwipeEnabled(false);
      },
      onStart: () => {
        this.props?.screenSlider.setSwipeEnabled(false);
      },
      onEnd: () => {
        this.props?.screenSlider.setSwipeEnabled(true);
      },
    });
  }
}

export const Screen2 = defineComponent<Screen2Component, IScreen2Props>("screen-2", Screen2Component);
