import { context } from "libraries/ui/context";

export type IWebComponent<P> = {
  setProps(props?: P): void;
  template(): HTMLElement | HTMLElement[];
};

export abstract class WebComponent<P> extends HTMLElement implements IWebComponent<P> {
  protected props!: P;

  private __context: unknown;

  public connectedCallback(): void {
    Array.from(context.entries()).forEach(([componentCtr, contextValue]) => {
      const tagName = (componentCtr as unknown as { tagName: string }).tagName;
      const contextElements = document.querySelectorAll(tagName);

      contextElements.forEach((contextElement) => {
        if (contextElement !== null && (contextElement === this || contextElement.contains(this))) {
          this.__context = contextValue;
        }
      });
    });

    this.render();
    this.mounted();
  }

  public disconnectedCallback(): void {
    this.unmounted();
  }

  protected mounted(): void {}

  protected unmounted(): void {}

  abstract template(): HTMLElement | HTMLElement[];

  public setProps(props: P): void {
    this.props = props;
  }

  public getContext<T>(): T {
    return this.__context as T;
  }

  private render(): void {
    const template = this.template();

    if (Array.isArray(template)) {
      this.replaceChildren(...template);

      return;
    }

    this.replaceChildren(template);
  }
}
