import { defineComponent } from "libraries/ui/define-component";
import { button } from "libraries/ui/html-elements";
import { render } from "libraries/ui/render";
import { WebComponent } from "libraries/ui/web-component";

interface IMenuProps {
  class: string;
  onFirstScreenButtonClick: () => void;
  onSecondScreenButtonClick: () => void;
  onThirdScreenButtonClick: () => void;
}

class MenuComponent extends WebComponent<IMenuProps> {
  public mounted(): void {
    this.classList.add(this.props.class);
  }

  public template(): HTMLElement[] {
    return render([
      button("1", {
        onclick: this.props.onFirstScreenButtonClick,
      }),
      button("2", {
        onclick: this.props.onSecondScreenButtonClick,
      }),
      button("3", {
        onclick: this.props.onThirdScreenButtonClick,
      }),
      button("4"),
    ]);
  }
}

export const Menu = defineComponent<MenuComponent, IMenuProps>("app-menu", MenuComponent);
