import { WebComponent } from "libraries/ui/web-component";
import * as styles from "app/components/app.module.css";
import { Header } from "app/components/header/header.component";
import { Content } from "app/components/content/content.component";
import { NumberService } from "features/number/services/number.service";
import { Menu } from "app/components/menu/menu.component";
import { defineComponent } from "libraries/ui/define-component";
import { IAppContext } from "app/components/app.component";
import { render } from "libraries/ui/render";
import { RouteName } from "app/router";

export interface IHomeScreenProps {}

class HomeScreenComponent extends WebComponent<IHomeScreenProps> {
  private get context(): IAppContext {
    return this.getContext<IAppContext>();
  }

  private onFirstScreenButtonClick(): void {
    this.context.contentScreenSlider.goToScreen(0);
  }

  private onSecondScreenButtonClick(): void {
    this.context.contentScreenSlider.goToScreen(1);
  }

  private onThirdScreenButtonClick(): void {
    this.context.contentScreenSlider.goToScreen(2);
  }

  private async onNextScreenButtonClick(): Promise<void> {
    this.context.history.push(this.context.url(RouteName.Next));
  }

  protected mounted(): void {
    this.classList.add(styles.homeScreen);
  }

  public template(): HTMLElement[] {
    return render([
      new Header({
        class: styles.header,
      }), //
      new Content({
        greeting: "hello world",
        getRandomNumber: new NumberService().getRandomNumber,
        class: styles.content,
        onNextScreenClick: () => {
          this.onNextScreenButtonClick();
        },
      }), //
      new Menu({
        class: styles.menu,
        onFirstScreenButtonClick: () => {
          this.onFirstScreenButtonClick();
        },
        onSecondScreenButtonClick: () => {
          this.onSecondScreenButtonClick();
        },
        onThirdScreenButtonClick: () => {
          this.onThirdScreenButtonClick();
        },
      }),
    ]);
  }
}

export const HomeScreen = defineComponent<HomeScreenComponent, IHomeScreenProps>("home-screen", HomeScreenComponent);
