import { Component } from "./component";
import { IWebComponent } from "libraries/ui/web-component";

export type ComponentCtr<P> = new (props: DefinedKeysOrVoid<P>) => Component;

export function defineComponent<T extends HTMLElement & IWebComponent<P>, P = {}>(
  tagName: string, //
  ctr: CustomElementConstructor //
): ComponentCtr<P> {
  customElements.define(tagName, ctr);

  const cls = class implements Component {
    private readonly htmlElement: T;

    constructor(props: DefinedKeysOrVoid<P>) {
      this.htmlElement = document.createElement(tagName) as T;

      this.htmlElement.setProps(props as P);
    }

    render(): HTMLElement {
      return this.htmlElement;
    }

    isComponent(): boolean {
      return true;
    }
  };

  (cls as unknown as { tagName: string }).tagName = tagName;

  return cls;
}
