.gridContainer {
  display: grid;
  /* Автоматическое заполнение колонок с минимальной шириной 5rem */
  grid-template-columns: repeat(auto-fill, minmax(5rem, 1fr));
  gap: 1rem; /* Отступ между плитками */
}

/* Стили для отдельных плиток */
.tile {
  border: 1px solid #000; /* Чёрная рамка */
  height: 8rem; /* Высота плитки */
  border-radius: 0.8rem; /* Скругление углов */
  background-color: #e0e0e0; /* Фон плитки */
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  color: #333;
}

.tile span {
  font-size: 4rem;
  user-select: none;
}

.tileGhost {
  opacity: 0;
}

.screen {
  padding-block: 1rem;
  overflow-y: scroll;
  scrollbar-width: none;
  -ms-overflow-style: none; /* IE and Edge */
  height: 100%;
  padding-inline: 1rem;
}

.screen::-webkit-scrollbar {
  display: none;
}
