enum Unit {
  Percent = "%",
  Px = "px",
}

function translateX(value: number, unit: Unit = "" as Unit): string {
  return `translate(${value}${unit})`;
}

interface ScreenTransitionInitOptions {
  container: HTMLElement;
  initialScreen: HTMLElement;
}

interface ScreenTransitionOptions {
  disabledAreaSelectors?: string[];
}

export class ScreenTransition {
  private container?: HTMLElement;
  private screensCache: HTMLElement[];
  private animations: Animation[];
  private currentScreenIndex: number;
  private touchStartX: number;
  private touchCurrentX: number;
  private isDragging: boolean;
  private isAnimating: boolean;
  private initialScreen?: HTMLElement;
  private options?: ScreenTransitionOptions;
  private touchStartY: number = 0;
  private touchCurrentY: number = 0;
  private isSwipingHorizontally: boolean | null = null;
  private swipeBackCallbacks: Array<() => void> = [];

  constructor(options?: ScreenTransitionOptions) {
    this.handleTouchStart = this.handleTouchStart.bind(this);
    this.handleTouchMove = this.handleTouchMove.bind(this);
    this.handleTouchEnd = this.handleTouchEnd.bind(this);

    this.touchStartX = 0;
    this.touchCurrentX = 0;
    this.currentScreenIndex = 0;
    this.isDragging = false;
    this.isAnimating = false;
    this.screensCache = [];
    this.animations = [];
    this.options = {
      disabledAreaSelectors: [],
      ...options,
    };
  }

  private get prevScreen(): HTMLElement {
    return this.screensCache[this.currentScreenIndex - 1];
  }

  private get currentScreen(): HTMLElement {
    return this.screensCache[this.currentScreenIndex];
  }

  private get canGoBack(): boolean {
    return this.currentScreen !== this.initialScreen;
  }

  private setupEventListeners(): void {
    if (this.container === undefined) {
      throw new Error("First need to execute the init method");
    }

    this.container.addEventListener("touchstart", this.handleTouchStart, { passive: false });
    this.container.addEventListener("touchmove", this.handleTouchMove, { passive: false });
    this.container.addEventListener("touchend", this.handleTouchEnd, { passive: false });
  }

  private removeEventListeners(): void {
    if (this.container === undefined) {
      throw new Error("No container element founs");
    }

    this.container.removeEventListener("touchstart", this.handleTouchStart);
    this.container.removeEventListener("touchmove", this.handleTouchMove);
    this.container.removeEventListener("touchend", this.handleTouchEnd);
  }

  private applyDefaultStylesToScreen(screen: HTMLElement, options: { zIndex?: string; transform?: string }) {
    const _options = {
      zIndex: "0",
      transform: translateX(0),
      ...options,
    };

    screen.style.position = "absolute";
    screen.style.height = "100%";
    screen.style.width = "100%";
    screen.style.top = "0";
    screen.style.left = "0";
    screen.style.transform = _options.transform;
    screen.style.zIndex = _options.zIndex;
  }

  private applyDefaultStylesToContainer(): void {
    if (this.container === undefined) {
      throw new Error("No container found");
    }

    this.container.style.position = "relative";
    this.container.style.overflow = "hidden";
    this.container.style.width = "100%";
    this.container.style.height = "100%";
  }

  private cancelAnimations() {
    this.animations.forEach((animation) => animation.cancel());
    this.animations = [];
  }

  private addPrevScreenToContainer(): void {
    this.currentScreen.before(this.prevScreen);
  }

  private removePrevScreenFromContainer(): void {
    this.prevScreen.remove();
  }

  private removeCurrentScreenFromContainer(): void {
    this.currentScreen.remove();
  }

  private removeCurrentScreenFromCache(): void {
    this.screensCache = this.screensCache.filter((screen) => {
      return screen !== this.currentScreen;
    });
  }

  private async animateScreen(
    screen: HTMLElement,
    translate: string,
    options?: {
      duration: number;
    }
  ): Promise<void> {
    this.isAnimating = true;
    const animation = screen.animate([{ transform: translate }], {
      duration: options?.duration ?? 150,
      easing: "cubic-bezier(0, 0, 0.7, 1)",
      fill: "forwards",
    });
    this.animations.push(animation);

    await animation.finished;
    animation.commitStyles();
    setTimeout(() => {
      this.cancelAnimations();
      this.isAnimating = false;
    }, 0);
  }

  public init(options: ScreenTransitionInitOptions) {
    this.container = options.container;
    this.initialScreen = options.initialScreen;
    this.applyDefaultStylesToContainer();

    this.screensCache.push(options.initialScreen);
    this.container.appendChild(this.currentScreen);
    this.applyDefaultStylesToScreen(this.currentScreen, {
      zIndex: String(this.screensCache.length),
    });
  }

  public async prev(): Promise<void> {
    if (this.container === undefined) {
      throw new Error("First need to execute the init method");
    }

    if (this.isAnimating) {
      return;
    }

    if (!this.canGoBack) {
      return;
    }

    this.cancelAnimations();
    this.addPrevScreenToContainer();
    await this.animateScreen(this.currentScreen, translateX(100, Unit.Percent));
    this.removeCurrentScreenFromContainer();
    this.removeCurrentScreenFromCache();
    this.currentScreenIndex -= 1;
  }

  public async next(screen: HTMLElement): Promise<void> {
    if (this.container === undefined) {
      throw new Error("First need to execute the init method");
    }

    if (this.isAnimating) {
      return;
    }

    this.cancelAnimations();

    this.screensCache.push(screen);
    this.applyDefaultStylesToScreen(screen, {
      zIndex: String(this.screensCache.length),
      transform: translateX(100, Unit.Percent),
    });
    this.container.appendChild(screen);
    await this.animateScreen(screen, translateX(0), {
      duration: 250,
    });
    this.removeCurrentScreenFromContainer();
    this.currentScreenIndex += 1;
    this.setupEventListeners();
  }

  public onSwipeBack(callback: () => void): void {
    this.swipeBackCallbacks.push(callback);
  }

  public offSwipeBack(callback: () => void): void {
    this.swipeBackCallbacks = this.swipeBackCallbacks.filter((cb) => cb !== callback);
  }

  private notifySwipeBackListeners(): void {
    this.swipeBackCallbacks.forEach((callback) => callback());
  }

  private isInDisabledArea(target: HTMLElement): boolean {
    const disabledAreas = this.options?.disabledAreaSelectors?.map((selector) => {
      return this.currentScreen.querySelector(selector) as HTMLElement;
    });

    return disabledAreas?.some((area) => area === target) ?? false;
  }

  private handleTouchStart(e: TouchEvent) {
    if (e.target !== null && this.isInDisabledArea(e.target as HTMLElement)) {
      return;
    }

    if (e.target === this.initialScreen || this.initialScreen?.contains(e.target as Node)) {
      return;
    }

    this.addPrevScreenToContainer();
    this.touchStartX = e.touches[0].clientX;
    this.touchStartY = e.touches[0].clientY;
    this.isSwipingHorizontally = null;
    this.isDragging = true;
    this.currentScreen.style.transition = "none"; // Отключаем переход во время перетаскивания
  }

  private handleTouchMove(e: TouchEvent) {
    if (e.target === this.initialScreen || this.initialScreen?.contains(e.target as Node)) {
      return;
    }

    if (!this.isDragging) {
      return;
    }

    this.touchCurrentX = e.touches[0].clientX;
    this.touchCurrentY = e.touches[0].clientY;

    let deltaX = this.touchCurrentX - this.touchStartX;
    let deltaY = this.touchCurrentY - this.touchStartY;

    if (this.isSwipingHorizontally === null) {
      if (Math.abs(deltaX) > Math.abs(deltaY)) {
        this.isSwipingHorizontally = true;
      } else {
        this.isSwipingHorizontally = false;
      }
    }

    if (!this.isSwipingHorizontally) {
      this.isDragging = false;
      return;
    }

    e.preventDefault();

    if (deltaX > 0) {
      this.currentScreen.style.transform = `translateX(${deltaX}px)`;
    }
  }

  private async handleTouchEnd(e: TouchEvent): Promise<void> {
    if (e.target !== null && this.isInDisabledArea(e.target as HTMLElement)) {
      return;
    }

    if (e.target === this.initialScreen || this.initialScreen?.contains(e.target as Node)) {
      return;
    }

    if (!this.isDragging || this.isSwipingHorizontally !== true) {
      return;
    }

    this.isDragging = false;

    let deltaX = this.touchCurrentX - this.touchStartX;
    let threshold = window.innerWidth / 3;

    if (deltaX > threshold) {
      await this.animateScreen(this.currentScreen, translateX(100, Unit.Percent));
      this.removeCurrentScreenFromContainer();
      this.removeCurrentScreenFromCache();
      this.currentScreenIndex -= 1;

      this.notifySwipeBackListeners();
    } else {
      await this.animateScreen(this.currentScreen, translateX(0));
      if (!this.isDragging) {
        this.removePrevScreenFromContainer();
      }
    }

    this.isSwipingHorizontally = null;
  }
  public destroy(): void {
    this.removeEventListeners();
    this.swipeBackCallbacks = [];
  }
}
